.home {
  display: flex;
  flex-direction: column;
}

.home__slider-container {
  width: 100%;
  margin: 0 auto;
  height: 40vh;
  position: relative;
  border-radius: 0.5rem;
}

.home__slide {
  position: absolute;
  width: 100%;
  display: grid;
  place-items: center;
  transition: all 0.25s ease-in-out;
  text-align: center;
}

.home__image {
 
  width: 100%;
  z-index: -1;
  margin-bottom: -300px;
  height: 600px;
  mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
 
}

.home__row {
  display: flex;
  z-index: 1;
  margin-left: 5px;
  margin-right: 5px;
}
/* ======== */

.background-image {
  width: 100%;
  height: 400px;
  background-repeat: no-repeat;
  background-position: left top;
  transition: background-position 0.5s;
}

.background-image.shift-right {
  background-position: right top;
}