.header {
    display: flex;
    flex-direction: column;
   
 }
  .header__top {
    height: 60px;
    background-color: #131921;
    color: white;
    display: flex;
    align-items: center;
    position: sticky;
    top: 0;
    z-index: 100;
    /* position: fixed; */
    top: 0;
  width: 100%
  }
  
  .header__left-nav {
    margin-left: 10px;
    padding: 5px 5px 2px 5px;
    color: white;
    border: 1px solid #666;
  }
  
  .header__logo {
    width: 100px;
    object-fit: contain;
    margin: 18px 20px 0 20px;
  }
  
  .header__search {
    display: flex;
    flex: 1;
    align-items: center;
    border-radius: 24px;
  }
  
  .header__searchInput {
    height: 36px;
    padding: 10px;
    border: none;
    width: 100%;
  }
  
  .header__searchInput:active {
    border: none;
  }
  
  .header__searchIcon {
    padding: 6px;
    height: 24px !important;
    background-color: #cd9042;
  }
  
  .header__nav,
  .header__nav span {
    display: flex;
    justify-content: center;
  }
  
  .header__link:first-child {
    width: 130px;
  }
  
  .header__link {
    color: white;
    text-decoration: none;
  }
  
  .header__option {
    height: 5vh;
    display: flex;
    flex-direction: column;
    margin: 0 10px;
  }
  
  .header__optionLineOne {
    font-size: 12px;
    color: whitesmoke;
  }
  
  .header__optionLineTwo {
    font-size: 14px;
    font-weight: 800;
    color: whitesmoke;
  }
  
  .header__optionBasket {
    display: flex;
    align-items: center;
    color: white;
  }
  
  .basket__count {
    font-size: 16px;
    border: 1px solid #fff;
    border-radius: 100px;
    margin: 0 15px 0 -10px;
    padding: 2px 4px;
  }
  
  .header__flag {
    width: 24px;
    transform: scale(1.5);
    position: relative;
    display: inline-block;
    height: 18px;
    margin-left: 15px;
    margin-right: 3px;
    object-fit: scale-down;
    background-image: url("../../img//usa-flag.png");
    background-position: 0 -166px;
  }
  
  .header__optionBasket > .MuiSvgIcon-root {
    width: 34px !important;
    height: 34px !important;
  }
  
  .header__bottom {
    display: flex;
    align-items: center;
    padding-top: 5px;
    padding-right: 15px;
    height: 40px;
    color: white;
    background-color: #232f3e;
  }
  
  .header__address {
    display: flex;
    color: white;
  }
  
  .header__address-icon {
    vertical-align: middle;
  }
  
  .header__address-icon > .MuiSvgIcon-root {
    font-size: 2rem;
  }
  
  .header__bottom-nav {
    display: flex;
    justify-content: space-evenly;
    flex: 1;
    justify-content: center;
   }
  
  .header__bottom-nav > span {
    margin-left: 10px;
    font-size: 14px;
    font-weight: 600;
}
.header__bottom-nav > span:hover{
  text-decoration: underline;
  cursor: pointer;
  font-weight: bolder;
  background-color:green;
  color: #fff;
}
  
  .header__left-nav > .MuiSvgIcon-root {
    font-size: 2em !important;
  }

  .header__bottom-app{
display: block;
  }

/* =============the new css  */



.header__bottom-nav .getspan {
  margin-right: 10px;
  font-size: 14px;
}

.header__bottom-app {
  margin-left: auto;
}

.header__bottom-image {
  width: 35px;
}

.header__link {
 
  font-weight: bold;
}

.header__link:hover {
  text-decoration: underline;
}
 

  
